.team_sec {
  margin: 2rem;
  text-align: center;
  font-family: "Roboto", sans-serif;
}
.teamsec_heading {
  font-size: 2.3rem;
  margin-bottom: 1rem;
  font-family: "logo";
}
.responsive-cell-block {
  min-height: 75px;
}
.responsive-container-block {
  min-height: 75px;
  height: fit-content;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 0px;
  margin-right: auto;
  margin-bottom: 0px;
  margin-left: auto;
  justify-content: space-evenly;
}
.img-wrapper {
  width: 100%;
}
.team-card {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.name {
  font-size: 1rem;
  color: white;
  margin-top: 0px;
  margin-right: 0px;
  margin-bottom: 5px;
  margin-left: 0px;
}
.position {
  font-size: 0.9rem;
  color: red;
  margin-top: 0px;
  margin-right: 0px;
  margin-bottom: 50px;
  margin-left: 0px;
}
.team-img {
  width: 50%;
  height: 100%;
}
.team-card-container {
  width: 280px;
  margin: 1rem 0 0 0;
}
.teamsec_btn {
  display: inline-block;
  padding: 12px;
  font-size: 15px;
  line-height: 1;
  background: white;
  color: black;
  font-weight: 600;
  letter-spacing: 0.03em;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: none;
}
.teamsec_btn:hover {
  background-color: red;
  color: black;
}
@media screen and (min-width: 1600px) {
  .teamsec_heading {
    font-size: 3.8rem;
    margin: 2rem;
  }
  .team-img {
    width: 60%;
  }
  .name {
    font-size: 1.5rem;
  }
  .position {
    font-size: 1.3rem;
  }
  .teamsec_btn {
    font-size: 1.8rem;
    padding: 2rem;
  }
}
@media (max-width: 500px) {
  .outer-container {
    padding: 10px 20px 10px 20px;
  }
  .section-head-text {
    text-align: center;
  }
  .team_sec {
    margin: 2rem 0;
  }
}

